.window {
    height: 450px;
    width: 100%;
    overflow: hidden;
}

.all_item_container {
    height: 100%;
    display: flex;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}

.left {
    position: relative;
    top: -80px;
    width: 50px;
    height: 50px;
    margin: 30px auto 80px auto;
    cursor: pointer;
}

.left:before,
.left:after {
    content: '';
    position: absolute;
    box-sizing: border-box;
    width: 114%;
    height: 100%;
    border-left: 16px solid #337AB7;
    border-bottom: 16px solid #337AB7;
    transform: rotate(45deg);
}


.right {
    position: relative;
    top: -80px;
    width: 50px;
    height: 50px;
    margin: 30px auto 80px auto;
    cursor: pointer;
}

.right:before,
.right:after {
    content: '';
    position: absolute;
    box-sizing: border-box;
    width: 113%;
    height: 100%;
    border-left: 16px solid #337AB7;
    border-bottom: 16px solid #337AB7;

    transform: rotate(-135deg);
}
