.footer {
    background: #303233;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    padding: 50px 0 0 0;
}

.list {
    margin: 0px 0 0 50px;
}

.list li {
    color: white;
    font-size: 14px;
    font-family: Arial;
    padding: 10px 15px;
}
@media(max-width: 600px){
    .list:first-child {
        display: none;
    }
}

.list li:first-child {
    font-weight: bold;
}

.social {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    height: 40px;
}

.vk a, .telegram a, .git a{
    padding: 10px 15px;
    color: white;
    text-decoration: none;
}

.vk {
    border: 2px solid white;
    border-radius: 10px;
    padding: 15px 15px;
    background: #234865;
    margin: 10px 15px;
}

.telegram {
    border: 2px solid white;
    border-radius: 10px;
    padding: 15px 15px;
    background: #53b1c4;
    margin: 10px 15px;
}
.git{
    border: 2px solid white;
    border-radius: 10px;
    padding: 15px 15px;
    background: black;
    margin: 10px 15px;
}