.card {
    margin: 15px 10px;
    padding: 15px 5px;
    border: 4px solid black;
    border-radius: 30px;
    height: 350px;
    width: 170px;
    position: relative;
}

.img {
    width: 150px;
    height: 150px;
    border-radius: 50px;
    border: 4px solid #934013;
}

.title {
    margin: 10px 0;
    font-size: 20px;
    text-align: center;
}

.text {
    padding: 10px;
}