.activeLink{
  color: #30638c;
}
.linkas{
  color: white;
}

a{
  color: black;
  text-decoration:none;
  padding: 10px 15px;
}
.modalClosse{
  position: absolute;
  width: 100%;
  transition: 3s ease all;
  transform: translateX(-100%);
  opacity: 0;
}

.modalOpen{
  position: absolute;
  width: 100%;
  transition: 3s ease all;
  transform: translateX(0%);
  opacity: 1;
}

.btn {
  position: absolute;
  top: 20px;
  right: 20px;
  margin-left: auto;
  height: 30px;
  width: 40px;
  cursor: pointer;
  z-index: 1000;
  background: #ffffff;
}

.btn span {
  width: 40px;
  height: 4px;
  background: #000000;
  position: absolute;
  top: 13px;
  left: 0;
}

.btn:after {
  content: '';
  width: 40px;
  height: 4px;
  background: #000000;
  position: absolute;
  top: 0;
  right: 0;
}

.btn:before {
  content: '';
  width: 40px;
  height: 4px;
  background: #000000;
  position: absolute;
  bottom: 0;
  right: 0;
}