.body {
    padding-top: 100px;
    display: flex;
    flex-wrap: wrap;
    user-select: none;

    max-width: 1200px;
    margin: 0 auto;
}

.block {
    border: 2px solid black;
    border-radius: 10px;

    width: 300px;
    height: 300px;

    display: flex;
    flex-wrap: wrap;


    padding: 10px 15px;
    margin: 10px 15px;
    background: rgba(0, 0, 0, 0.47);
    transition: all .25s ease-in;
}
.block:hover {
    transform: translateY(-5px);
    border: 2px solid black;
    border-radius: 10px;

    width: 320px;
    height: 320px;

    display: flex;
    flex-wrap: wrap;


    padding: 10px 15px;
    margin: 10px 15px;
    transition: all .25s ease-in;
}


.email {
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    display: flex;
    margin-bottom: 20px;

    color: #61b9ff;
}

.text {
    font-size: 14px;
    font-family: "Arial Unicode MS";
}
.page{
    display: flex;
    opacity: 0;
    overflow: hidden;
}
.page div{
    padding: 5px;
    cursor: pointer;
}

